.floor-plans {
	padding: 5rem 0;

	& > .container {
	    @include breakpoint(small down) {
	    	padding: 0;
	    }
	} // & > .container
} // .floor-plans

.floor-plans__tab-nav {
    padding: 0;
    max-width: 47.5rem;
    background-color: $color-b;
    justify-content: center;
    list-style: none;
    margin: 0 auto 4rem;
    padding: 1.75rem 2rem;

    @include breakpoint(large down) {
    	max-width: calc(100% - 3rem);
    }

    & > li {

    	@include breakpoint(small down) {
    		margin-bottom: 1rem;
    	}

    	&:last-of-type {
    		@include breakpoint(small down) {
    			margin-bottom: 0;
    		}
    	}

        & > .button {
        	margin: 0 0.75rem;
        	background-color: transparent;
        	border: 1px solid white;
        	color: $color-f;

        	@include breakpoint(small down) {
        		display:block;
        		width: 100%;
        		margin: 0;
        	}

        	&:hover {
        		background-color: $color-f;
        		color: $color-b;
        	}

        	&.is-active {
        	    background-color: $color-f;
        	    color: $color-a;
        	} // &.is-active

        } // & > .button

    } // & > li

} // .floor-plans__tab-nav

.floor-plan__categories {
    padding: 0;
    justify-content: center;
    margin-bottom: 4rem;
    transition: all 0.4s ease;

    @include breakpoint(medium down) {
    	height: 40px;
    	overflow: hidden;
    	margin-bottom: 2rem;
    }

    @include breakpoint(small down) {
    	max-width: calc(100% - 3rem);
    	margin: 0 auto 0.15rem;
    }

    &.is-disabled {
        opacity: 0.3;
        pointer-events: none;
    } // &.is-disabled

    &.is-open {
        @include breakpoint(medium down) {
        	height: auto;
        	padding-bottom: 2rem;
        }
    } // &.is-open

    & > .floor-plans__selected {
    	display: none;
        height: 40px;
        width: 100%;
        text-align: center;
        cursor: pointer;

        @include breakpoint(medium down) {
        	display:block;
        }

        span {
        	font: 900 0.95rem/1 $font-primary;
        	letter-spacing: 0.15em;
		    color: $color-a;
		    text-transform: uppercase;

		    @include breakpoint(medium down) {
		    	border: none;
		    	color: $color-a;
		    	padding-right: 2rem;
		    	background: url('../../img/icon-chevron-down.svg') no-repeat 100% calc(50% - 1px) / 16px;
		    }

        } // span

    } // & > .floor-plan__selected

    & > li {
        margin: 0 0.75rem;
        display: inline-block;
        list-style: none;

        @include breakpoint(medium down) {
        	display:block;
        	width: 100%;
        	text-align: center;
        }

    } // & > li

    .button {
        border: 1px solid $color-b;
        color: $color-b;
        background: transparent;
        transition: all 0.3s ease;
        font-size: 0.9rem;
        letter-spacing: 0.2em;
        padding-left: 1.1rem;
        padding-right: 1.1rem;

	    @include breakpoint(medium down) {
	    	border: none;
	    	color: $color-a;
	    }

        &:hover {
        	background-color: $color-b;
        	color: $color-f;

        	@include breakpoint(medium down) {
        		color: $color-c;
        		background-color: transparent;
        	}

        }

        &.is-active {
        	background-color: $color-b;
        	color: $color-f;

        	@include breakpoint(medium down) {
        		color: $color-a;
        		background-color: transparent;
        	}

        } // &.is-active

    } // button

} // .floor-plans__tab-nav