.hot-spots {
	width: 100%;
} // .hot-spots

.hot-spots__filters {
    @include xy-cell($size: 4, $gutters: 0);
    background-color: $color-b;
    padding: 4rem;
} // .hot-spots__filters


#hot-spots-map {
    height: 650px;
    width: 100%;
    display:block;
} // #hot-spots-map

.hot-spots__map {
    @include xy-cell($size: 8, $gutters: 0);

    // infobox triangle.
    .si-pointer-top {
        background-color: transparent;
        border-top-color: transparent !important;

        &:before {
        	content: '';
        	position: absolute;
        	left: 50%;
        	top: calc(100% - 24px);
        	width: 1px;
        	height: 40px;
        	background-color: $color-b;
        	z-index: 99;
        }
    } // .si-pointer-top

    .si-content {
    	position: relative;
        text-align: center;
        padding: 1rem;
    } // .si-content

    .si-close-button {
        z-index: 999;
    } // .si-close-button

    .marker__title {
    	color: $color-f;
    	margin: 0 0 0.75rem;
    	text-transform: uppercase;
    	letter-spacing: 0.1em;
    	font: 800 0.95rem/1 $font-primary;
    } // .marker__title

    .marker__address {
        & > a {
        	display:block;
            color: $color-f;
            font: 600 0.775rem/1.5 $font-primary;
            text-transform: uppercase;
            letter-spacing: 0.05em;
        } // & > a
    } // .marker__address

    .marker__directions {
        background-color: $color-f;
        border-color: $color-f;
        color: $color-b;
        text-transform: uppercase;
        padding: 0.5rem 0.75rem;
        display:block;
        width: 100%;
        border-radius: 0;
        font: 600 0.775rem/1.2 $font-primary;
        letter-spacing: 0.1em;
        transition: all 0.3s ease;

        &:hover {
        	background-color: $color-c;
        	border-color: $color-c;
        	color: $color-f;
        }
    } // .marker__directions

} // .hot-spots__map

.hot-spots__category-group {
    padding: 0;
    list-style: none;
    margin-bottom: 2rem;

    &.is-active {

    	.hot-spot__category-posts {
    		padding: 1rem 0 0 0;
    		max-height: 130px;
    		height: 130px;
    		overflow: auto;

    		& > li {
    		    animation: fadeInUp 0.5s forwards;
    		    margin-bottom: 1rem;
    		    z-index: 1;

    		    &:last-of-type {
    		    	margin-bottom: 0;
    		    }

    		} // & > li

        } // .hot-spot__category-posts

    } // &.is-active

} // .hot-spots__category-group

.hot-spots__category-name {
    color: $color-f;
    background-color: transparent;
    border: 0;
    text-transform: uppercase;
    font: 700 1rem/1 $font-primary;
    letter-spacing: 0.15em;
    padding: 0;
} // .hot-spots__category-name


.hot-spot__category-posts::-webkit-scrollbar {
	display: inline-block;
	position: relative;
    width: 0.4rem;

    @include breakpoint(medium down) {
        height: 10px;
    }

    @include breakpoint(small down) {
        height: 10px;
    }
}

.hot-spot__category-posts::-webkit-scrollbar-track {
    border: 1px solid $color-a;
}

.hot-spot__category-posts::-webkit-scrollbar-thumb {
    background-color: $color-a;
    outline: 0px solid $color-a;
}

.hot-spot__category-posts {
	position: relative;
    list-style: none;
    padding: 0;
    max-height: 0;
    transition: all 0.5s ease;
    padding-right: 0;

    & > li {
    	position: relative;
        color: $color-f;
        font: 400 0.85rem/1.25 $font-primary;
        letter-spacing: 0.075em;
        opacity: 0;
        cursor: pointer;
        transition: all 0.3s ease;
        z-index: -1;

        &.is-active {
            color: $color-c;
        } // &.is-active

    } // & > li

} // .hot-spot__category-posts
