.floor-plan-card {
	@include xy-cell($size: 4, $gutters: 1.5rem, $gutter-type: padding, $gutter-position: right left);
	padding: 4rem 0;
	display: none;

	@include breakpoint(medium down) {
		@include xy-cell($size: 6, $gutters: 1.5rem, $gutter-type: padding, $gutter-position: right left);
	}

	@include breakpoint(small down) {
		@include xy-cell($size: 12, $gutters: 0, $gutter-type: padding, $gutter-position: top bottom right left);
		margin-bottom: 2rem;
		padding: 3rem 2rem;
	}

	&.is-active {
	    display: block;
	} // &,.is-active

} // .floor-plan-card

.floor-plan-card__image {
	position: relative;
	margin-bottom: 1.25rem;
	padding-right: 3rem;
	height: 280px;

	&.is-placeholder {

		&:after {
			background-color: $color-e;
			width: calc(100% - 3rem);
			height: 100%;
			background-image: none;
			left: 0;
			right: auto;
		}

		& > span {
			text-align: center;
			position: relative;
			top: 50%;
			transform: translateY(-50%);
			color: rgba($color-a, 0.25);
			z-index: 2;
			display: block;
			font: 700 1rem/1 $font-primary;
			letter-spacing: 0.1em;
			text-transform: uppercase;
		} // & > span

	} // &.is-placeholder

	&:after {
		content: '';
		position: absolute;
		right: 0;
		top: 0;
		width: 16px;
		height: 16px;
		background: transparent url('../../img/icon-plus-square.svg') no-repeat 50% 50% / 100%;
		cursor: pointer;
	}

    picture,
    img {
        // width: 100%;
        height: auto;
        display:block;
        margin: 0 auto 0 0;
        max-width: 100%;
		max-height: 280px;
		width: auto;
        cursor: pointer;
		object-fit: fill;
    } // picture, img

    img {
    } // img

} // .floor-plan-card__image

.floor-plan-card__name {
    text-transform: uppercase;
    font: 900 1.2rem/1 $font-primary;
    letter-spacing: 0.2em;
    margin-bottom: 0.85rem;
} // .floor-plan-card__name

.floor-plan-card__details {
    padding: 0;
    list-style: none;
    margin-bottom: 1.25rem;

    & > li {
        display: inline-block;
        font: 600 0.85rem/1 $font-primary;
        letter-spacing: 0.15em;
        padding: 0 0.8rem;
        border-right: 1px solid $color-d;
        margin-bottom: 1rem;

        &:first-of-type {
        	padding-left: 0;
        }

        &.floor-plan-card__size {
        } // &.floor-plan-card__size

        &.floor-plan-card__price {
        	border-right: 0;
        } // &.floor-plan-card__price

    } // & > li

} // .floor-plan-card__details

.floor-plan-card__button {

    & > .button {
        background-color: $color-b;
        border-color: $color-b;
        padding: 0.75rem 1.5rem 0.65rem;
        letter-spacing: 0.1em;
        font-size: 0.9rem;

        &:hover {
        	background-color: $color-a;
        	border-color: $color-a;
        }

    } // & > .button
} // .floor-plan-card__button