.floor-plans__filters {
    width: 100%;
    position: relative;
    padding: 0 0 4rem;
    max-width: 65rem;
    margin: 0 auto;

    .filters__data {
        @include xy-cell($size: 12);
        padding: 0;
        list-style: none;
        font: 900 1rem/1 $font-primary;
        text-align: center;

        @include breakpoint(medium down) {
        	@include xy-cell($size: 12);
        	text-align: center;
        }

        @include breakpoint(small down) {
        	font: 900 0.8rem/1 $font-primary;
        }

        & > span {
            display: inline-block;
            color: $color-a;
            margin-right: 1.5rem;

            @include breakpoint(medium down) {
            	display:block;
            	width: 100%;
            	margin-bottom: 0.8rem;
            }

        } // & > span

        & > li {
        	position: relative;
            display: inline-block;
            margin-right: 1.5rem;
            // font: 900 1rem/1 $font-primary;
            transition: all 0.3s ease;
            cursor: pointer;
            padding-bottom: 0.33rem;

            &:last-of-type {
            	margin-right: 0;
            }

            &:after {
            	content: '';
            	position: absolute;
            	left: 0;
            	right: 0;
            	bottom: -0.25rem;
            	width: 0;
            	height: 1px;
            	background-color: $color-a;
            	transition: all 0.25s ease;

            	@include breakpoint(medium down) {
            		bottom: -0.1rem;
            	}

            }

            @include breakpoint(small down) {
            	font: 900 0.8rem/1 $font-primary;
            }

            &:hover {
            	color: $color-b;
            }

            &.is-active {
                &:after {
                	width: 100%;
                }

                &:hover {
                	color: $color-a;
                }

            } // &.is-active

        } // & > ul

    } // .filter__data

} // .floor-plan__filters